import React from 'react'
import { Logo } from '@ya.praktikum/react-developer-burger-ui-components'
import headerStyles from './Header.module.css'
import NavigationComponent from '../NavigationComponent/NavigationComponent'

export default function Header() {
  return (
    <header className={headerStyles.content}>
      <div className={headerStyles.leftPanels}>
        <NavigationComponent  text="" icon="burger" />
        <NavigationComponent text=" " icon="list" />
      </div>
      <Logo className={headerStyles.logo}/>
      <div className={headerStyles.rightPanel}>
        <NavigationComponent text=" " icon="profile" />
      </div>
    </header>
  )
}
